import './App.css';
import 'react-toastify/dist/ReactToastify.css';
import {createTheme, ThemeProvider} from "@mui/material/styles";
import {Routes, Route, Navigate} from "react-router-dom";
import {NavBar} from "./component/layout/NavBar";
import CssBaseline from "@mui/material/CssBaseline";
import {Home} from "./component/main/Home";
import {Login} from "./component/main/Login";
import {Registration} from "./component/main/Registration";
import {Forgot} from "./component/main/Forgot";
import {Reset} from "./component/main/Reset";
import {useSelector} from "react-redux";
import SuccessPage from "./component/main/SuccessPage";
import {Rules} from "./component/main/Rules";

function App() {
    const {isAuth} = useSelector(state => state.auth)

   const theme = createTheme();
  return (
      <ThemeProvider theme={theme}>
          <CssBaseline />
        <NavBar/>
        <Routes>
          <Route path="/" element={<Home />}/>
          <Route path="login" element={isAuth ? <Navigate to='/account' /> :<Login/>} />
          <Route path="registration" element={isAuth ? <Navigate to='/' /> :<Registration/>} />
          <Route path="forgot" element={isAuth ? <Navigate to='/' /> :<Forgot/>} />
          <Route path="reset" element={<Reset/>} />
          <Route path="rules" element={ <Rules/>} />
          <Route path="account" element={!isAuth ? <Navigate to='/' /> :<SuccessPage/>} />
        </Routes>
      </ThemeProvider>
  );
}

export default App;
