import {createAsyncThunk} from "@reduxjs/toolkit";
import $api from "../../API";
import axios from "axios";


export const registrationAC  = createAsyncThunk(
    'auth/registration',
    async (data, thunkAPI) => {
              try {
            const response = await $api.post('registration', data)
            return response.data
        } catch (e) {
            return thunkAPI.rejectWithValue(e?.response?.data)
        }
    }
)

export const loginAC = createAsyncThunk(
    'auth/login',
    async (data, thunkAPI) => {
        try{
            const response= await $api.post('login', data)
            localStorage.setItem('token', response?.data?.accessToken);
            return response.data
        }
        catch (e) {
            return thunkAPI.rejectWithValue(e?.response ? e?.response?.data : 'incorrect email address or password')
        }
    }
)

export const logoutsAC = createAsyncThunk(
    'auth/logout',
    async ( thunkAPI) => {
        try{
            const response= await $api.post("logout")
            localStorage.removeItem('token');
            localStorage.removeItem('applicationState');
            // localStorage.clear()
            return response.data
        }
        catch (e) {
            console.log(e.message)
            return thunkAPI.rejectWithValue('404 error')
        }
    }
)
