import * as React from 'react';
import {InputElement} from "../Elements/InputElement";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import {Checkbox, FormControlLabel} from "@mui/material";
import {requisitesAC} from "../../store/actions/RequisitesActions";
import {useDispatch, useSelector} from "react-redux";
import {notifyError, notifySuccess} from "../Utils/ToastNotify";
import {ToastContainer} from "react-toastify";
import {resetRequisites} from "../../store/reducers/RequisitesReducer";
import {useNavigate} from "react-router-dom";


export const DetailsForm = ({details={}}) => {
    const dispatch = useDispatch()
    const navigate = useNavigate();
    const [value, setValue] = React.useState({
        email: '',
        FIO: '',
        card: '',
        isConsent: false,
    })
    const {button={} ,tab=[],title=""} = details
const {load,error,success='',message=''} = useSelector(state => state.requisites)

    React.useEffect(()=>{
        if(success){
            notifySuccess(message)
            setValue({
                email: '',
                FIO: '',
                card: '',
                isConsent: false,
            })
            setTimeout(()=>{
                dispatch(resetRequisites())
            },5000)
        }
        if(error){
            notifyError(message)
            setTimeout(()=>{
                dispatch(resetRequisites())
            },5000)
        }

    },[error,success])
    const [err, setErr] = React.useState([])
    React.useEffect(()=>{
        if(Array.isArray(error)){
            setErr(error)
        }else {
            setErr([])
        }

    },[error])
    const helper = Object.fromEntries(
        err.map(el => [el.param, {
            ...el
        }])
    )
    let helperTextEmail =helper.email
    let helperTextFIO = helper.FIO;
    let helperTextCard = helper.card;
    let helperTextIsConsent = helper.isConsent;

    const handleSubmit = (event) => {
        event.preventDefault();
        dispatch(requisitesAC(value))
    };

    return (
        <Container component="main" maxWidth="xs">
            <ToastContainer />
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    width:'100%'
                }}
            >
                <Typography  variant={'h6'} style={{fontWeight:'bold',width:'100%'}}>
                    {title}
            </Typography>
                <Box component="form" onSubmit={handleSubmit} noValidate sx={{mt: 1,width:"100%"}}>
                       <InputElement error={helperTextEmail ? true : false}
                                     helperText={helperTextEmail?.msg}
                                     value={value.email}
                                     onChange={(e)=>setValue({...value,email:e.target.value})}
                                     label={tab[0]?.input_label} placeholder={tab[0]?.input_label} name="email"/>
                       <InputElement margin="15px 0"
                                     error={helperTextFIO ? true : false}
                                     helperText={helperTextFIO?.msg}
                                     value={value.FIO}
                                     onChange={(e)=>setValue({...value,FIO:e.target.value})}
                                     label={tab[1]?.input_label} placeholder={tab[1]?.input_label} name="FIO"/>
                       <InputElement margin="10px 0 15px 0"
                                     error={helperTextCard ? true : false}
                                     helperText={helperTextCard?.msg}
                                     value={value.card}
                                     onChange={(e)=>setValue({...value,card:e.target.value})}
                                     label={tab[2]?.input_label} placeholder={tab[2]?.input_label} name="card"/>

                    <FormControlLabel sx={{margin:"10px 0"}}
                        control={<Checkbox name='isConsent'
                              checked={value.isConsent} onChange={()=>setValue({...value,isConsent:!value.isConsent})}
                            sx={{
                            color:helperTextIsConsent ? "#d32f2f" : "#00adb5",
                            borderRadius:5,
                            '&.Mui-checked': {
                                color: helperTextIsConsent ? "#d32f2f" : "#00adb5",
                            },
                        }} />}
                        label={<Typography component={"span"} style={{fontSize:13}}>
                            {tab[3]?.lab_title}
                            <Typography onClick={()=>navigate('rules')}  component={"span"} style={{fontSize:13,color:helperTextIsConsent ? "#d32f2f" : "#00adb5",}}>
                            {tab[3]?.checkbox_label}
                        </Typography></Typography>}
                        labelPlacement="end"
                    />

                    <Button variant={'contained'} type='submit'
                            disabled={load}
                            color='inherit'
                            style={{
                                backgroundColor:load ? "#7adee3" : "#00ADB5",
                                color: "#fff",
                                width: "100%",
                                borderRadius:"8px",
                                height:"48px"
                            }}>{button?.title}</Button>
                </Box>
            </Box>
        </Container>
    );
}
