import {createSlice} from "@reduxjs/toolkit"
import {CheckAuth, forgotAC, loginAC, logoutsAC, registrationAC, resetAC} from "../actions/AuthActions";


const initialState = {
    isAuth:false,
    users: {},
    load: false,
    success:false,
    error: false,
    message:''
}

const authSlice = createSlice({
    name: "auth",
    initialState,
    reducers: {
        reset(state) {
           return initialState
        }
    },
    extraReducers: {
        [registrationAC.pending]: (state) => {
            state.load = true
            state.error = false
        },
        [registrationAC.fulfilled]: (state, action) => {
            state.load = false
            state.error = false
            state.success = action?.payload?.msg
            state.isAuth = false
        },
        [registrationAC.rejected]: (state, action) => {
            state.load = false
            state.isAuth = false
            state.error = action?.payload?.errors
            state.message = action?.payload?.message
        },
        [loginAC.pending]: (state) => {
            state.load = true
            state.error = false
        },
        [loginAC.fulfilled]: (state, action) => {
            state.load = false
            state.users = action?.payload?.user
            state.error = false
            state.success = true
            state.isAuth = true
        },
        [loginAC.rejected]: (state, action) => {
            state.load = false
            state.isAuth = false
            state.error = action?.payload?.errors
            state.message = action?.payload?.message
        },
        [forgotAC.pending]: (state) => {
            state.load = true
            state.error = false
        },
        [forgotAC.fulfilled]: (state, action) => {
            state.load = false
            state.error = false
            state.success = action?.payload?.msg
            state.isAuth = false
        },
        [forgotAC.rejected]: (state, action) => {
            state.load = false
            state.isAuth = false
            state.error = action?.payload?.errors
            state.message = action?.payload?.message
        },
        [resetAC.pending]: (state) => {
            state.load = true
            state.error = false
        },
        [resetAC.fulfilled]: (state, action) => {
            state.load = false
            state.error = false
            state.success = action?.payload?.message
            state.isAuth = false
        },
        [resetAC.rejected]: (state, action) => {
            state.load = false
            state.isAuth = false
            state.error = action?.payload?.errors
            state.message = action?.payload?.message
        },
        [CheckAuth.pending]: (state) => {
            state.load = true
        },
        [CheckAuth.fulfilled]: (state, action) => {
            state.load = false
            state.users = action?.payload?.user
            state.error = false
            state.success = true
            state.isAuth = true
        },
        [CheckAuth.rejected]: (state, action) => {
            state.load = false
            state.isAuth = false
            state.error = true
            state.message = action.payload
        },
        [logoutsAC.pending]: (state) => {
            state.load = true
        },
        [logoutsAC.fulfilled]: () => initialState,

        [logoutsAC.rejected]: (state, action) => {
            state.load = false
            state.isAuth = false
            state.error = action.payload
        },
    }

})

export const {reset} = authSlice.actions


export default authSlice.reducer
