import {createAsyncThunk} from "@reduxjs/toolkit";
import $api from "../../API";



export const requisitesAC  = createAsyncThunk(
    'auth/requisites',
    async (data, thunkAPI) => {
              try {
            const response = await $api.post('requisites', data)
            return response.data
        } catch (e) {
            return thunkAPI.rejectWithValue(e?.response?.data)
        }
    }
)
