import * as React from 'react';
import { styled } from '@mui/material/styles';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";

const AntTabs = styled(Tabs)({
    minHeight:'40px',
       // borderBottom: '1px solid #e8e8e8',
    '& .MuiTabs-indicator': {
        // backgroundColor: '#1890ff',
         backgroundColor: 'transparent',
    },
});

const AntTab = styled((props) => <Tab disableRipple {...props} />)(({ theme }) => ({
    textTransform: 'none',
    fontSize:'13px',
    minWidth: 0,
    [theme.breakpoints.up('sm')]: {
        minWidth: 0,
    },
    fontWeight: 'bold',
    marginRight: theme.spacing(1),
    color: '#00adb5',
    backgroundColor:'#fff',
    fontFamily: [
        '-apple-system',
        'BlinkMacSystemFont',
        '"Segoe UI"',
        'Roboto',
        '"Helvetica Neue"',
        'Arial',
        'sans-serif',
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"',
    ].join(','),
    '&:hover': {
        // color: '#fff',
        opacity: 1,
    },
    '&.Mui-selected': {
        color: '#fff',
        backgroundColor:'#00adb5',
        fontWeight: theme.typography.fontWeightMedium,
    },
    '&.Mui-focusVisible': {
        backgroundColor: '#d1eaff',
    },
}));

const StyledTabs = styled((props) => (
    <Tabs
        {...props}
        TabIndicatorProps={{ children: <span className="MuiTabs-indicatorSpan" /> }}
    />
))({
    '& .MuiTabs-indicator': {
        display: 'flex',
        justifyContent: 'center',
        backgroundColor: 'transparent',
    },
    '& .MuiTabs-indicatorSpan': {
        maxWidth: 40,
        width: '100%',
        backgroundColor: 'transparent',
    },

});

const StyledTab = styled((props) => <Tab disableRipple {...props} />)(
    ({ theme }) => ({
        textTransform: 'none',
        fontWeight: theme.typography.fontWeightRegular,
        fontSize: theme.typography.pxToRem(15),
        marginRight: theme.spacing(1),
        color: 'rgba(255, 255, 255, 0.7)',
        '&.Mui-selected': {
            color: '#fff',
        },
        '&.Mui-focusVisible': {
            backgroundColor: 'rgba(100, 95, 228, 0.32)',
        },

    }),
);

export default function SuccessPage() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Container component="main" maxWidth="lg" sx={{paddingTop:10}}>
            <Typography variant={'h2'} sx={{textAlign:'center'}}>Personal account</Typography>
        <Box sx={{
            display: 'flex',
            flexDirection: 'column',
             alignItems: 'center',
            width: '100%',
            background: 'rgb(0, 173, 181)',
            boxShadow: 'rgb(0 0 0 / 10%) 0px 4px 16px',
            borderRadius: '9px'
        }}>
            <Box sx={{ width: '100%',
                margin: 0,
                display: 'flex',
                padding: 0,
                background:' rgb(255, 255, 255)',
                borderRadius: '8px',
                justifyContent: 'center',
                WebkitBoxPack: 'center',}}>
                <AntTabs value={value} onChange={handleChange} aria-label="ant example">
                    <AntTab label="Orders" />
                    <AntTab label="Referral program" />
                    <AntTab label="Details" />
                    <AntTab label="Settings" />
                </AntTabs>
            </Box>
            <Box >
                <StyledTabs
                    value={value}
                    onChange={handleChange}
                    aria-label="styled tabs example"
                >
                    <StyledTab label="Workflows" />
                    <StyledTab label="Datasets" />
                    <StyledTab label="Connections" />
                </StyledTabs>
            </Box>
        </Box>
        </Container>
    );
}
