import * as React from 'react';
import Box from "@mui/material/Box";
import {AppBar} from "../Elements/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import Typography from "@mui/material/Typography";
import LoginIcon from '@mui/icons-material/Login';
import LogoutIcon from '@mui/icons-material/Logout';
import {SelectedFlag} from "../Elements/SelectedFlag";
import { useNavigate } from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {logoutsAC} from "../../store/actions/AuthActions";

export const NavBar = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch()
    const [open, setOpen] = React.useState(false);
    const {isAuth} = useSelector(state => state.auth)
    const toggleDrawer = () => {
        setOpen(!open);
    };
const logInAndOut = () =>{
    if(isAuth){
        dispatch(logoutsAC())
        navigate("/")
    }else{
        navigate("/login")
    }
   }
    return (
        <Box sx={{ display: 'flex' }}>
            <AppBar position="static" open={open} elevation={0}>
                <Toolbar
                    sx={{
                        pr: '24px', // keep right padding when drawer closed
                    }}
                >
                    <IconButton
                        edge="start"
                        color="inherit"
                        aria-label="open drawer"
                        onClick={toggleDrawer}
                        sx={{
                            marginRight: '36px',
                            // ...(open && { display: 'none' }),
                        }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography
                        onClick={()=>navigate("/")}
                        component="h1"
                        variant="h4"
                        color="inherit"
                        noWrap
                        sx={{ flexGrow: 1 ,color:"#f08000", fontWeight:"bold",cursor:"pointer",
                            background: '-webkit-linear-gradient(#88e9e4, #f08000)',
                            WebkitBackgroundClip: 'text',
                            WebkitTextFillColor: 'transparent'}}
                    >
                       TradeCrypto
                    </Typography>
                    <IconButton color="inherit" onClick={logInAndOut}>
                        {isAuth ? <LogoutIcon style={{transform: 'rotate(-180deg)'}}/>
                            :<LoginIcon/>}
                    </IconButton>
                    <SelectedFlag/>
                </Toolbar>
            </AppBar>
        </Box>
    )
}
