import {createSlice} from "@reduxjs/toolkit"
import {requisitesAC} from "../actions/RequisitesActions";


const initialState = {
    load: false,
    success:false,
    error: false,
    message:''
}

const requisitesSlice = createSlice({
    name: "requisites",
    initialState,
    reducers: {
        resetRequisites(state) {
           return initialState
        }
    },
    extraReducers: {
        [requisitesAC.pending]: (state) => {
            state.load = true
            state.error = false
        },
        [requisitesAC.fulfilled]: (state, action) => {
            state.load = false
            state.error = false
            state.success = true
            state.message = action?.payload?.msg
        },
        [requisitesAC.rejected]: (state, action) => {
            state.load = false
            state.error = action?.payload?.errors
            state.message = action?.payload?.message
            state.success = false
        }
    }

})

export const {resetRequisites} = requisitesSlice.actions


export default requisitesSlice.reducer
