import * as React from "react";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import {InputElement} from "../Elements/InputElement";
import Button from "@mui/material/Button";
import {useDispatch, useSelector} from "react-redux";
import {useNavigate} from "react-router-dom";
import {registrationAC} from "../../store/actions/AuthActions";
import {ToastContainer} from "react-toastify";
import {notifyError, notifySuccess} from "../Utils/ToastNotify";
import {reset} from "../../store/reducers/AuthReducer";


export const Registration = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const {registration} = useSelector(state => state.language.arr);
    const { button = {}, tab = [], title = "", description = "",register="", forget="",attention={}} = registration;
     const {load,error,success='',message=''} = useSelector(state => state.auth);
const [err, setErr] = React.useState([])
    React.useEffect(()=>{
        if(Array.isArray(error)){
            setErr(error)
        }else {
            setErr([])
        }

    },[error])
    React.useEffect(()=>{
        if(success){
            notifySuccess(success)
            navigate('/login')
            setTimeout(()=>{
                dispatch(reset())
            },5000)
        }
        if(error){
            notifyError(message)
            setTimeout(()=>{
                dispatch(reset())
            },5000)

        }

    },[error,success])

    const helper = Object.fromEntries(
        err.map(el => [el.param, {
            ...el
        }])
    )

    let helperTextEmail =helper.email
    let helperTextPassword = helper.password;
    let helperTextConfirmPsw = helper.confirmPsw;

    const handleSubmit = (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        dispatch(registrationAC({
            email: data.get('email'),
            password: data.get('password'),
            confirmPsw: data.get('confirmPsw'),
        }))
    };

    return (
        <Container component="main" maxWidth="xs">
            <ToastContainer />
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    width: '100%'
                }}
            >
                <Typography variant={'body1'} style={{fontSize: 50, margin: "0 0 20px  0"}}>
                    {title}
                </Typography>
                <Typography variant={'h6'} style={{fontSize: 13, margin: "0 0 20px  0"}}>
                    {description}
                </Typography>
                <Box component="form" onSubmit={handleSubmit} noValidate autoComplete="off"
                     sx={{mt: 1, width: "100%", display: "flex", flexDirection: 'column', alignItems: 'center',}}>
                    <InputElement error={helperTextEmail ? true : false}  helperText={helperTextEmail?.msg} label={tab[0]?.input_label} placeholder={tab[0]?.input_label} name="email"/>
                    <InputElement  error={helperTextPassword ? true : false}  helperText={helperTextPassword?.msg} label={tab[1]?.input_label} placeholder={tab[1]?.input_label} name="password"/>
                    <InputElement  error={helperTextConfirmPsw ? true : false}  helperText={helperTextConfirmPsw?.msg} label={tab[2]?.input_label} placeholder={tab[2]?.input_label} name="confirmPsw"/>

                    <Button variant={'contained'} type='submit'
                            disabled={load}
                            color='inherit'
                            style={{
                                margin: '20px 0',
                                backgroundColor: load ? "#7adee3" : "#00ADB5",
                                color: "#fff",
                                width: "50%",
                                borderRadius: "8px",
                                height: "48px"
                            }}>{button?.title}</Button>
                </Box>
                <Box sx={{display:"flex", justifyContent:"space-around", width:"100%"}}>
                    <Typography variant={'h6'} onClick={()=>navigate('/')} style={{fontSize:13,color:"#00adb5",cursor:"pointer"}} >
                        {forget}
                    </Typography>
                    <Typography variant={'h6'} onClick={()=>navigate('/login')} style={{fontSize:13,color:"#00adb5",cursor:"pointer"}} >
                        {register}
                    </Typography>
                </Box>
                <Box sx={{textAlign:"center",margin:"40px 0"}}>
                <Typography component={"span"} style={{fontSize:12,fontWeight:"bold"}}>
                    {attention.title}
                    <Typography  component={"span"} style={{fontSize:12,color:"#3e4046"}}>
                        {attention.desc}
                    </Typography>
                </Typography>
                </Box>
            </Box>
        </Container>
    );
}
